<template>
  <div id="app" class="vh-100">
    <div class="container-fluid bg-yellow">
      <h1 class="p-2">Business case</h1>
      <div class="container text-start bg-yellow">
        <div class="row">
          <div class="col-lg"></div>
          <div class="col-lg col-md">
            <div class="mb-3">
              <label for="hej" class="form-label">Actual stock</label>
              <input type="text" class="form-control bg-yellow border-white color-white" v-model="input.actualStock" />
            </div>
            <div class="mb-3">
              <label for="exampleInputEmail1" class="form-label">Gross intake(per month)</label>
              <input type="text" class="form-control bg-yellow border-white color-white" v-model="input.grossIntake" />
            </div>
            <div class="mb-3">
              <label for="exampleInputEmail1" class="form-label">Churn(per month)</label>
              <input type="text" class="form-control bg-yellow border-white color-white" v-model="input.churn" />
            </div>
            <div class="mb-3">
              <label for="exampleInputEmail1" class="form-label">ARPU(per month)</label>
              <input type="text" class="form-control bg-yellow border-white color-white" v-model="input.arpu" />
            </div>
            <div class="mb-3">
              <label for="exampleInputEmail1" class="form-label">Months</label>
              <input type="text" class="form-control bg-yellow border-white color-white" v-model="input.months" />
            </div>
          </div>
          <div class="col-lg"></div>
        </div>
      </div>
      <br /><br /><br />
    </div>
    <div class="container-fluid bg-green h-100">
      <br /><br /><br />
      <div class="container bg-green">
        <div class="row">
          <div class="col">
            <table class="table color-white">
              <thead>
                <tr>
                  <th scope="col">Month</th>
                  <th scope="col">Stock</th>
                  <th scope="col">SR</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in sr" v-bind:key="index">
                  <td>{{ item.month }}</td>
                  <td>{{ item.stock }}</td>
                  <td>{{ item.val.toLocaleString() }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td></td>
                  <td></td>
                  <td>
                    <b>Total: {{ srSum.toLocaleString() }}</b>
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>

    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view/> -->
  </div>
</template>

<script>
export default {
  name: "Main",
  components: {},
  data() {
    return {
      input: {
        actualStock: "",
        grossIntake: "",
        churn: "",
        arpu: "",
        months: "",
      },
    };
  },
  methods: {},
  computed: {
    sr() {
      var result = [];
      for (var i = 0; i < parseInt(this.input.months); i++) {
        result.push({
          month: i,
          stock: parseInt(this.input.actualStock) + parseInt(this.input.grossIntake) * i - parseInt(this.input.churn) * i,
          val: (parseInt(this.input.actualStock) + parseInt(this.input.grossIntake) * i - parseInt(this.input.churn) * i) * parseInt(this.input.arpu),
        });
      }
      return result;
    },
    srSum() {
      var res = 0;
      this.sr.forEach((item) => (res = res + item.val));
      return res;
    },
    srChartData() {
      var res = [];
      this.sr.forEach((item) => {
        res.push({ label: item.month, value: item.val });
      });
      return res;
    },
  },
  mounted() {},
};
</script>

